
import { defineComponent } from "vue";
import { exChangeAtrribe } from "@/utils/tool";
const sizeTable: any = {
  small: "btn-sm",
  large: "btn-lg",
};
export default defineComponent({
  name: "w-button",
  props: {
    size: {
      type: String,
    },
  },
  setup(props, {attrs}) {
    const sizeClass = exChangeAtrribe("size", props, attrs, sizeTable);
    return { sizeClass };
  },
});
